import React, { Component } from 'react';
import { Card, Image, Progress, Header, Segment, Grid, Divider, Icon, Feed } from 'semantic-ui-react'

const userImage = 'http://www.iconarchive.com/download/i58697/dakirby309/windows-8-metro/Folders-OS-User-No-Frame-Metro.ico';

export default class MyPerformance extends Component {
    render() {
        return(
            <Card id='my-performance'>
                <Card.Content>
                    <Image floated='right' size='mini' src={userImage} />
                    <Card.Header>Usuário</Card.Header>
                    <Card.Meta>Meu Desempenho</Card.Meta>
                </Card.Content> 
                <Card.Content>                        
                    <Card.Description>
                        <Segment basic>
                            <Grid columns={2} relaxed='very' stackable>
                                <Grid.Column>
                                    <PerformanceProgress title='Minha Meta' percent='80'/>
                                    <PerformanceProgress title='Minha Região' percent='50'/>
                                </Grid.Column>
                                <Grid.Column>
                                    <FeedUser icon='star' date='Ontem' summary='Você recebeu uma boa avaliação'/>
                                    <FeedUser icon='star' date='Ontem' summary='Coleta Seletiva :)'/>
                                    <FeedUser icon='mail' date='22/01' summary='Mensagem!!'/>
                                </Grid.Column>
                            </Grid>
                            <Divider vertical>
                                <Icon name='heart' />                                        
                            </Divider>
                        </Segment>
                    </Card.Description>
                </Card.Content>                    
            </Card>
        );
    }
}

class PerformanceProgress extends Component {        
    render() {
        return (
        <div>
            <Header size='tiny'>{this.props.title}</Header>
            <Progress percent={this.props.percent} progress='percent' indicating />            
        </div>
        );
    }
}

class FeedUser extends Component {
    render() {
        return (
            <Feed>
                <Feed.Event icon={this.props.icon} date={this.props.date} summary={this.props.summary} />
            </Feed>
        );
    }
}