import React, { Component } from 'react';
import { Item, Label, Dimmer, Loader, Segment } from 'semantic-ui-react'

const imagem = 'https://react.semantic-ui.com/images/wireframe/image.png';

export default class Guide extends Component {    

    constructor() {
        super();
        this.state = {
            materials: [],
            isLoading: true,
        };
    }

    componentDidMount() {
        fetch('/api/materials')
            .then(response => response.json())
            .then(list => {                
                this.setState({materials: list, isLoading: false});        
            });
    }

    render() {
        if (this.state.isLoading) {
            return (
                <Segment basic className='loading-screen'>
                    <Dimmer active inverted>
                        <Loader />
                    </Dimmer>
                </Segment>
            )
        }
        return (
            <article>
                <h3> Guia de Materiais </h3>
                <div id='content'>
                    <Item.Group divided>
                        {this.state.materials.map(m => 
                            <MaterialListItem material={m}/>
                        )}
                    </Item.Group>
                </div>            
            </article>
        );
    }
}

class MaterialListItem extends Component {
    render() {        
        const material = this.props.material;        
        return (            
            <Item>
                <Item.Image src={imagem} />
                <Item.Content>
                    <Item.Header as='a'> {material.nome} </Item.Header>
                    <Item.Meta>
                    <span className='cinema'>{material.nomeTipoMaterial}</span>
                    </Item.Meta>
                    <Item.Description>
                        {material.descricao === null ? <span> Sem Descrição </span> : <span> material.descricao </span>}
                    </Item.Description>
                    <Item.Extra>
                        {
                            (material.reciclavel) ?
                            <Label color='teal'> Reciclável </Label> :
                            <Label color='brown'> Não Reciclável </Label>
                        }                    
                    </Item.Extra>
                </Item.Content>
            </Item>            
        );
    }
}