import React, { Component } from 'react';
import { Dropdown, Icon, Menu, Image, Input } from 'semantic-ui-react'
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import './App.css';
import Home from './pages/Home';
import Region from './pages/Region';
import Days from './pages/Days';
import Guide from './pages/Guide';

const userImage = 'http://www.iconarchive.com/download/i58697/dakirby309/windows-8-metro/Folders-OS-User-No-Frame-Metro.ico';

class App extends Component {
  render() {
    return (
      <div className="App">
        <Router>
          <header>
            <Topo/>
          </header>    
          <div id='page'>
            <aside>
              <SideMenu/>                 
            </aside>
            <main>
              <Route exact path="/" component={Home} />
              <Route exact path="/region" component={Region} />
              <Route exact path="/days" component={Days} />
              <Route exact path="/guide" component={Guide} />
            </main>          
          </div>      
        </Router>
      </div>
    );
  }
}

class Topo extends Component {
  render() {
    return (
      <div>
        <Menu inverted>
          <Menu.Menu>
            <Menu.Item header name='Sistema' icon='leaf'/>
          </Menu.Menu>          

          <Menu.Menu position='right'>
            <Menu.Item fitted='vertically'>
              <Image src={userImage} avatar />              
            </Menu.Item>  

            <Dropdown item text='Usuário'>
              <Dropdown.Menu>            
                <Dropdown.Item>Perfil</Dropdown.Item>
                <Dropdown.Item>Sobre</Dropdown.Item>                
              </Dropdown.Menu>
            </Dropdown>
          </Menu.Menu>
        </Menu>
      </div>
    );
  }
}

class SideMenu extends Component {
  state = { activeItem: '' }
  handleItemClick = (e, { name }) => this.setState({ activeItem: name })
  render() {    
    const { activeItem } = this.state;
    return (
      <nav>
        <Menu pointing vertical>
          <Menu.Item>
            <Input icon='search' placeholder='Pesquisar' />
          </Menu.Item>

          <Menu.Item name='home' as={Link} to='/' active={activeItem === 'home'} onClick={this.handleItemClick}>            
            <Icon name='home' />
            Home
          </Menu.Item>

          <Menu.Item name='guide' as={Link} to='/guide' active={activeItem === 'guide'} onClick={this.handleItemClick}>
            <Icon name='list ul' />
            Guia de Materiais            
          </Menu.Item>

          <Menu.Item name='days' as={Link} to='/days' active={activeItem === 'days'} onClick={this.handleItemClick}>
            <Icon name='calendar alternate outline' />
            Dias de Coleta
          </Menu.Item>

          <Menu.Item name='region' as={Link} to='/region' active={activeItem === 'region'} onClick={this.handleItemClick}>
            <Icon name='map marker alternate' />
            Sua Região
          </Menu.Item>        
        </Menu>
      </nav>
    );
  }
}

export default App;
